import React from 'react';
import styles from './Projects.module.css';
import ProjectsItemFirst from '../.././components/Projects/ProjectsItemFirst/ProjectsItemFirst';
import ProjectsItemSecond from './ProjectsItemSecond/ProjectsItemSecond';

const Projects = () => {
    return (
        <div className={styles.projects}>
            <div className={styles.projects__title}>My Projects</div>
            <div className={styles.container}>
                <div className={styles.projects__items}>

                    <ProjectsItemFirst />
                    <ProjectsItemSecond />
                </div>
            </div>
        </div>
    );
}

export default Projects;